import axios from 'axios'
import $store from '@/store/index'
import $router from '@/router/index'
// å¼•å…¥element-uiå¼¹çª—
import { Message } from 'element-ui'

// ä¸ºå›¾ç‰‡å‡†å¤‡åŸºåœ°å€
export const baseURL = 'http://big-event-vue-api-t.itheima.net'

// åˆ›å»ºä¸€ä¸ªè‡ªå®šçš„axiosæ–¹æ³•(æ¯”åŸaxioså¤šäº†ä¸ªåŸºåœ°å€)
// axioså‡½æ•°è¯·æ±‚çš„urlåœ°å€å‰é¢ä¼šè¢«æ‹¼æ¥åŸºåœ°å€, ç„¶åaxiosè¯·æ±‚baseURL+urlåå°å®Œæ•´åœ°å€
const reqAxios = axios.create({
  baseURL: baseURL
})

// ä½¿ç”¨axiosæ‹¦æˆªå™¨å¤„ç†apiæ¥å£è°ƒç”¨æ—¶ï¼Œéƒ¨åˆ†å‚æ•°é‡å¤è°ƒç”¨é—®é¢˜
reqAxios.interceptors.request.use(
  function (config) {
    // åœ¨å‘é€è¯·æ±‚ä¹‹å‰åšäº›ä»€ä¹ˆ
    // è°ƒç”¨æ¥å£æ—¶ç»Ÿä¸€ä¸ºè¯·æ±‚å¤´æŒ‚è½½ Authorization å­—æ®µ
    // console.log(config)

    if ($store.state.token) {
      config.headers.Authorization = $store.state.token
    }
    return config
  },
  function (error) {
    // å¯¹è¯·æ±‚é”™è¯¯åšäº›ä»€ä¹ˆ
    return Promise.reject(error)
  }
)

// è§£å†³tokenè¿‡æœŸé—®é¢˜
reqAxios.interceptors.response.use(
  function (response) {
    // 2xx èŒƒå›´å†…çš„çŠ¶æ€ç éƒ½ä¼šè§¦å‘è¯¥å‡½æ•°ã€‚
    // å¯¹å“åº”æ•°æ®åšç‚¹ä»€ä¹ˆ
    return response
  },
  function (error) {
    // è¶…å‡º 2xx èŒƒå›´çš„çŠ¶æ€ç éƒ½ä¼šè§¦å‘è¯¥å‡½æ•°ã€‚
    // å¯¹å“åº”é”™è¯¯åšç‚¹ä»€ä¹ˆ
    if (error.response.status === 401) {
      // console.dir(error)
      // æ— æ•ˆçš„ token
      // æŠŠ Vuex ä¸­çš„ token é‡ç½®ä¸ºç©ºï¼Œå¹¶è·³è½¬åˆ°ç™»å½•é¡µé¢
      $store.commit('updateToken', '')
      $router.push('/login')

      // å¼¹çª—æç¤º
      Message.error('ç”¨æˆ·èº«ä»½ä»¥è¿‡æœŸï¼ï¼')
    }
    return Promise.reject(error)
  }
)

// å¯¼å‡ºè‡ªå®šä¹‰çš„axiosæ–¹æ³•, ä¾›å¤–é¢è°ƒç”¨ä¼ å‚å‘è¯·æ±‚
export default reqAxios
