import Vue from 'vue'
import VueRouter from 'vue-router'

import $store from '@/store/index'

Vue.use(VueRouter)

// è§£å†³ç¼–ç¨‹å¼è·¯ç”±å¾€åŒä¸€åœ°å€è·³è½¬æ—¶ä¼šæŠ¥é”™çš„æƒ…å†µğŸ—
const originalPush = VueRouter.prototype.push
const originalReplace = VueRouter.prototype.replace

// push
VueRouter.prototype.push = function push(location, onResolve, onReject) {
  // if (onResolve || onReject) {
  //   return originalPush.call(this, location, onResolve, onReject)
  // }
  return originalPush.call(this, location).catch((err) => err)
}

// replace
VueRouter.prototype.replace = function push(location, onResolve, onReject) {
  // if (onResolve || onReject) {
  //   return originalReplace.call(this, location, onResolve, onReject)
  // }
  return originalReplace.call(this, location).catch((err) => err)
}

const routes = [
  {
    path: '/',
    redirect: '/login'
  },
  {
    path: '/login',
    // ä½¿ç”¨ è·¯ç”±âœ¨æ‡’åŠ è½½
    component: () => import('@/views/login/comLogin.vue')
  },
  {
    path: '/register',
    component: () => import('@/views/register/comRegister.vue')
  },
  {
    path: '/layout',
    // ä½¿ç”¨ è·¯ç”±âœ¨æ‡’åŠ è½½
    component: () => import('@/views/layout/comLayout.vue'),
    redirect: '/layout/home',
    children: [
      {
        path: 'home',
        component: () => import('@/components/home.vue')
      },
      {
        path: 'user-info',
        component: () => import('@/components/person-center/userInfo.vue')
      },
      {
        path: 'user-avatar',
        component: () => import('@/components/person-center/changePic.vue')
      },
      {
        path: 'user-pwd',
        component: () => import('@/components/person-center/resetPassword.vue')
      },
      {
        path: 'art-cate',
        component: () => import('@/components/article-manage/articleCase.vue')
      },
      {
        path: 'art-list',
        component: () => import('@/components/article-manage/articleList.vue')
      }
    ]
  }
]

const router = new VueRouter({
  routes
})

const whiteList = ['/login', '/register']

// å‘ç”Ÿé¡µé¢è·³è½¬æ—¶è§¦å‘
router.beforeEach((to, from, next) => {
  const token = $store.state.token

  // æƒé™è®¿é—®æ§åˆ¶
  if (token) {
    // å¦‚æœæœ‰token, è¯æ˜å·²ç™»å½•
    if (!$store.state.userInfo.username) {
      // æœ‰tokenä½†æ˜¯æ²¡æœ‰ç”¨æˆ·ä¿¡æ¯, æ‰å»è¯·æ±‚ç”¨æˆ·ä¿¡æ¯ä¿å­˜åˆ°vuexé‡Œ
      // è°ƒç”¨actionsé‡Œæ–¹æ³•è¯·æ±‚æ•°æ®
      $store.dispatch('initUserInfo')
      // ä¸‹æ¬¡åˆ‡æ¢é¡µé¢vuexé‡Œæœ‰ç”¨æˆ·ä¿¡æ¯æ•°æ®å°±ä¸ä¼šé‡å¤è¯·æ±‚ç”¨æˆ·ä¿¡æ¯
    }
    next() // è·¯ç”±æ”¾è¡Œ
  } else {
    // å¦‚æœæ— token
    // ã€å› ä¸ºç™»å½•ã€æ³¨å†Œé¡µé¢ä¹Ÿæ²¡æœ‰tokenï¼Œæ ¹æ®å…¨å±€å‰ç½®å®ˆå«çš„å®šä¹‰ä¸åšç™½åå•å¤„ç†ï¼Œä¼šä¸€ç›´å¤„äºè·³è½¬â€”â€”è§¦å‘â€”â€”è·³è½¬å¾ªç¯ä¸­ã€‘
    // å¦‚æœå»çš„æ˜¯ç™½åå•âœ¨é¡µé¢, åˆ™æ”¾è¡Œ
    if (whiteList.includes(to.path)) {
      next()
    } else {
      // å¦‚æœå…¶ä»–é¡µé¢è¯·å¼ºåˆ¶æ‹¦æˆªå¹¶è·³è½¬åˆ°ç™»å½•é¡µé¢
      next('/login')
    }
  }
  next()
})

export default router
